<template>
    <div class="areaForLoader">
        <div class="container">
            <section class="loading-cube">
                <div class="cube cube3d">
                    <div class="front"></div>
                    <div class="back"></div>
                    <div class="top"></div>
                    <div class="bottom"></div>
                    <div class="left"></div>
                    <div class="right"></div>
                </div>
            </section>
        </div>
    </div>
</template>

<script>
export default {
    mounted() {
        $(window).on("load", function () {
            $(".areaForLoader").delay(3000).fadeOut("slow");
            document.body.style.overflow = "hidden"; 
        });
        const cube = document.querySelector("div.cube.cube3d");

        setTimeout(() => {
            cube.classList.add("lotation");
            
        }, 2600);
        setTimeout(() => {
            document.body.style.overflow = ""; 
        }, 4000);
    },
};
</script>

